.Navbar {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.9);
  padding: 2em 70px;
  width: calc(100% - 140px);
  height: calc(100vh - 4em);
  display: none;
  transform-origin: bottom;
  z-index: 1;
}

.Navbar img {
  float: right;
  width: 30px;
  margin: 10px 0;
  cursor: pointer;
  opacity: 0;
}

.Navbar ul {
  margin: 20% 0 auto;
  padding: 0;
  display: grid;
  grid-template-columns: auto auto auto auto;
  list-style-type: none;
}

.Navbar ul li {
  margin: 0;
  padding: 0;
  display: inline-block;
  opacity: 0;
}

.Navbar ul li a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 45px;
  transition: 0.5s;
}

.Navbar ul li a:hover {
  color: gray;
}

.Navbar ul li span {
  color: gray;
  display: block;
  width: 250px;
  cursor: default;
}

.display {
  display: block;
  animation: forNavContainer 0.4s ease-in forwards;
}

.display img {
  animation: closeBtn 1s 1s forwards;
}

.display ul li:nth-of-type(1) {
  animation: navElements 1s 0.7s forwards;
}

.display ul li:nth-of-type(2) {
  animation: navElements 1s 0.9s forwards;
}

.display ul li:nth-of-type(3) {
  animation: navElements 1s 1.1s forwards;
}

.display ul li:nth-of-type(4) {
  animation: navElements 1s 1.3s forwards;
}

@keyframes forNavContainer {
  from {
    transform: scaleY(0);
  }
  to {
    transform: scaleY(1);
  }
}

@keyframes closeBtn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes navElements {
  from {
    transform: translateY(70%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@media only screen and (max-width: 768px) {
  .Navbar {
    padding: 1em 20px;
    width: calc(100% - 40px);
    height: calc(100vh - 2em);
    transform-origin: left;
  }

  .Navbar img {
    margin-top: 6px;
  }

  .Navbar ul {
    display: grid;
    grid-template-columns: none;
    grid-template-rows: 80px 80px 80px 80px;
    text-align: center;
  }

  .Navbar ul li span {
    display: none;
  }

  @keyframes forNavContainer {
    from {
      transform: scaleX(0);
    }
    to {
      transform: scaleX(1);
    }
  }

  @keyframes navElements {
    from {
      transform: translateX(-70%);
      opacity: 0;
    }
    to {
      transform: translateX(0);
      opacity: 1;
    }
  }
}
