.Header {
  overflow: hidden;
  margin: 0;
  padding: 2em 70px;
}

.Header p {
  margin: 0;
  padding: 0;
  float: left;
  font-size: 30px;
  color: white;
  font-weight: 600;
  text-decoration: none;
}

.Header img.menu {
  float: right;
  width: 30px;
  margin: 10px 0;
  cursor: pointer;
}

.Header img.logo {
  width: 125px;
}

@media only screen and (max-width: 768px) {
  .Header {
    padding: 1em 20px;
  }

  .Header img.logo {
    width: 80px;
  }
}
