.ProjChilds {
  margin: 1em;
  display: grid;
  grid-gap: 1em;
  grid-template-columns: repeat(3, 1fr);
}

@media only screen and (max-width: 1024px) {
  .ProjChilds {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media only screen and (max-width: 425px) {
  .ProjChilds {
    grid-template-columns: repeat(1, 1fr);
  }
}
