.Projects {
  position: relative;
  padding: 0;
  margin: 0 auto;
  width: 80vw;
  height: 75vh;
  background: rgba(0, 0, 0, 0.7);
  overflow: scroll;
}

.breadcrumbs {
  width: 80vw;
  position: sticky;
  top: 0;
  margin: auto;
  overflow: hidden;
  background: rgba(189, 195, 199, 0.8);
}

.breadcrumbs ul {
  margin: 0;
  padding: 0;
  text-align: center;
}

.breadcrumbs ul li {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: inline-block;
}

.breadcrumbs ul li a {
  text-decoration: none;
  cursor: default;
}

.breadcrumbs ul li a button {
  margin: 0px 20px;
  padding: 10px;
  font-size: 25px;
  color: white;
  border: none;
  background: rgb(33, 33, 33);
  outline: none;
  cursor: pointer;
}

.breadcrumbs ul li a button:hover {
  background: rgba(255, 255, 255, 0.3);
}

.breadcrumbs ul li a button:active {
  background: rgba(255, 255, 255, 0.5);
}

@media only screen and (max-width: 1024px) {
  .Projects {
    height: 85vh;
  }
}
