.Home {
  margin: 0;
  padding: 0;
  /* position: relative; */
}

.wrapper {
  margin: 0;
  padding: 2em;
  padding-left: 1em;
  top: 10vh;
  position: relative;
  width: 60%;
  margin: auto;
  overflow: hidden;
  border-left: 4px solid rgb(153, 212, 231);
  background-color: rgba(0, 0, 0, 0.548);
}

.Home .wrapper p.quote {
  margin: 0;
  padding: 0;
  color: white;
  font-family: "EB Garamond", serif;
  font-size: 55px;
}

.Home .wrapper p.comma {
  margin: 0;
  padding: 0;
  font-size: 70px;
  font-weight: 600;
  line-height: 0.7;
  color: white;
}

.Home .wrapper p.credit {
  margin: 0;
  padding: 0;
  color: white;
  text-align: right;
}

@media only screen and (max-width: 768px) {
  .Home .wrapper p.quote {
    font-size: 35px;
  }

  .Home .wrapper p.comma {
    font-size: 50px;
  }
}

@media only screen and (max-width: 425px) {
  .Home .wrapper p.quote {
    font-size: 25px;
  }

  .Home .wrapper p.comma {
    font-size: 40px;
  }
}
