.Card {
  background: rgba(223, 223, 223, 0.8);
  padding: 0.3em;
  border-radius: 8px;
  overflow: hidden;
}

.Card img {
  width: 100%;
  border-radius: 8px;
}

.Card .details {
  padding: 0.3em;
}

.Card .details .links {
  height: 3em;
  width: 100%;
  border-radius: 8px;
  overflow: hidden;
  display: grid;
  grid-template-columns: auto auto auto;
  background-color: #000;
}

.Card .details .links a {
  margin: 0;
  padding: 0;
}

.Card .details .links a button {
  margin: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(233, 233, 233);
  font-family: "Roboto", sans-serif;
  font-size: 16px;
  cursor: pointer;
  outline: none;
  border: none;
}

.Card .details .links a:nth-of-type(2) button {
  border: 1px solid rgb(223, 223, 223);
  border-top: none;
  border-bottom: none;
}

.Card .details .links a button:active {
  background-color: rgb(150, 150, 150);
}

.Card .details .links a button:disabled {
  color: rgb(136, 136, 136);
}
